<template>
    <modal
        class='component-actionsheet'
        :show='show'
        mode='bottom'
        :title='title'
        :maskClose='maskClose'
        :closeButton='false'
        @close='emitClose'>
        <div class="component-actionsheet__title" v-if='title'>
            <span>{{title}}</span>
            <img class="component-actionsheet_close_btn" src="../../assets/img/classroom3/close_down.png" @click='emitClose'>
        </div>
        <div class="component-actionsheet__content">
            <slot></slot>
        </div>
        <!-- <footer class="component-actionsheet__cancle component-actionsheet__item" @click='emitClose'>取消</footer> -->
    </modal>
</template>

<script>
    import modal from '../../components/Modal.vue'
    import buttonLoading from '../../components/ButtonLoading.vue'

    export default {
        props:{
            loading:{
                type: Boolean,
                default: false
            },
            maskClose:{
                type: Boolean,
                default:true
            },
            tips:{
                type: String,
                default:''
            },
            title:{
                type: String,
                default:''
            },
            show:{
                type: Boolean,
                required: true,
            },
            closeText:{
                type: String,
                default: '取消',
            },
            confirmText:{
                type: String,
                default: '确定',
            }
        },
        data() {
            return {

            }
        },
        methods:{
            emitClose(){
                this.$emit('close')
            },
            emitCancle(){
                this.$emit('close')
                this.$emit('cancle')
            },
            emitConfirm(){
                this.$emit('confirm')
            }
        },
        components:{ modal, buttonLoading }
    }
</script>

<style lang="scss">
    @import "../../sass/variables";
    @import "../../sass/extends";
    @import "../../sass/mixins";

    .component-actionsheet{
        background-color: rgba(0,0,0,0.38)!important;
        .component-modal__main{
            padding: 0.24rem;
            padding-bottom: 0;
            box-sizing: border-box;
            background-color: $backgroundColor;
        }
        .component-actionsheet__item{
            background-color: #fff;
            border-bottom:1px solid $sectionColor;
            position: relative;
            padding: 10px 0;
            text-align: center;
            font-size: 18px;
            color:$importColor;
            line-height:1.5;
            &:last-child{
                border-bottom: none;
            }
            &:active{
                background-color: $backgroundColor;
            }
        }
        .component-actionsheet__cancle{
            margin-top: 6px;
        }
    }
    .component-actionsheet__title {
        font-family: PingFangSC-Medium;
        font-size: 0.2rem;
        color: rgba(0,0,0,0.87);
        line-height: 0.3rem;
        display: flex;
        justify-content: space-between;
    }
    .component-actionsheet_close_btn {
        width: 0.24rem;
        height: 0.24rem;
    }
</style>





