<template>
	<div>
		<audio id="audio" class="play_audio" controls preload="auto" ref="audio">
			<source src="../../assets/audio/class1.mp3" type="audio/mpeg">
			您的浏览器不支持 audio 元素。
		</audio>
		<div @click="handleClickPlay">播放</div>
		<div @click="add10">增加10</div>
		<!-- <div @click="handleClickPlay">暂停</div> -->
		<div class="loan_num_range">
          <div class="loan_num_range_btn" id="loan_num_range_btn">
            <div class="loan_num_range_icon" @touchmove="handleTouchmove"></div>
          </div>
          <div class="loan_num_range_basic">
            <div class="loan_num_range_step">
              <div class="loan_num_range_step_item"></div>
              <div class="loan_num_range_step_item loan_num_range_step_item_mid"></div>
              <div class="loan_num_range_step_item"></div>
            </div>
            <div class="loan_num_range_twig"></div>
            <div class="loan_num_range_scale">
              <span>500元</span>
              <span>200000元</span>
            </div>
          </div>
        </div>
	</div>
</template>

<script>
	export default {
		methods: {
	      	handleTouchmove(e) {
		        e = e || window.event;
		        let left = e.changedTouches[0].clientX - ($(window).width() - $('.loan_num_range').width()) / 2 - $('#loan_num_range_btn').width() / 2;
		        if(left < -3) {
		          	left = -3;
		        }
		        if(left > 259) {
		          	left = 259;
		        }
		        $('#loan_num_range_btn').css({left});
		        let amountNum = Math.round((left + 3) / 262 * 2000) * 100;
		        this.$refs.audio.currentTime = (left + 3) / 262 * this.$refs.audio.duration;
	      	},
	      	handleClickPlay() {
	      		this.$refs.audio.play();
	      		console.log(this.$refs.audio.duration);
	      	},
	      	add10() {
	      		this.$refs.audio.currentTime += 10;
	      	}
	    }
	}
</script>

<style scoped>
	
  .loan_num_range {
    margin: 0 auto;
    position: relative;
    padding-top: 0.16rem;
    width: 280px;
  }
  .loan_num_range_btn {
    position: absolute;
    bottom: 16px;
    left: -3px;
  }
  .loan_num_range_icon {
    margin: 0 auto;
    margin-top: 3px;
    width: 24px;
    height: 24px;
    background: url('../../assets/img/sliding.png') top no-repeat;
    background-size: 100%;
  }
  .loan_num_range_twig {
    width: 280px;
    height: 8px;
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    background: #D3B899;
  }
  .loan_num_range_scale {
    margin-top: 8px;
    width: 280px;
    height: 17px;
    line-height: 17px;
    display: flex;
    justify-content: space-between;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: rgba(0,0,0,0.38);
  }
  .loan_num_range_step {
    margin: 0 auto;
    width: 264px;
    height: 10px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .loan_num_range_step_item {
    width: 2px;
    height: 10px;
    background: #D3B899;
  }
  .loan_num_range_step_item_mid {
    height: 6px;
  }
</style>


