<script>
	import methods from '../mixins/methods'
	import api from '../api/classroom'
	export default {
		data() {
			return {
				isComment: false,
				isCommented: false,
				hasGrade: false,
				grade1: 0,
				grade2: 0,
				grade3: 0,
				grade4: 0,
				prod: {
				    "article": {},
				    "productTags": [
				      {}
				    ],
				},
				goodList: [],
				mediumList: [],
				badList: [],
				isSelectLoan: true,
				selectType: '',
				bottom: localStorage.getItem('bottom')
			}
		},
		mixins: [methods],
		computed: {
			gradeSum() {
				return (this.grade1 + this.grade2 + this.grade3 + this.grade4) * 5;
			},
			successRate() {
				return Math.round(this.prod.succeedNum / (this.prod.succeedNum + this.prod.rejectedNum) * 100);
			},
			failRate() {
				return 100 - this.successRate;
			},
			article() {
				return this.prod.article;
			}
		},
		watch: {
	  		article(newValue, oldValue) {
	  			if(JSON.stringify(newValue) !== '{}') {
	  				this.$nextTick(() => {
	  					let length = $('.guide_text img').length;
	  					for(let i = 0; i < length; i++) {
	  						let width = $('.guide_text img').eq(i).attr('width');
	  						let height = $('.guide_text img').eq(i).attr('height');
	  						$('.guide_text img').eq(i).css({
	  							width: '3.12rem',
	  							height: (3.12 / width * height).toFixed(2) + 'rem'
	  						})
	  					}
	  				})
	  			}
	  		}
		},
		methods: {
			handleClickConfirm() {
				if(this.grade1 > 0 && this.grade2 > 0 && this.grade3 > 0 && this.grade4 > 0) {
					this.submitComment(1);
					this.isComment = false;
				}else {
					this.$message('请先完成评分');
				}
			},
			scoring() {
				if(!this.hasGrade) {
					sa.track('conGrade_click', {
						loanname: this.prod.platformName
					});
					this.isComment = true;
				}
			},
			select(status) {
				sa.track('spy_click', {
					loanname: this.prod.platformName
				})
				this.isSelectLoan = false;
				this.selectType = status + 2;
				this.submitComment(status + 2);
			},
			apply() {
				sa.track('recAd_click', {
	  				loanname: this.prod.platformName
	  			})
	  			setTimeout(() => {
	  				location.href = this.prod.loanUrl;
	  			}, 300)
			},
			getDetail() {
				let data = {
					deviceId: localStorage.getItem('randomId') || ''
				}
				api.requestGet(`/headline-product/products/${this.$route.query.id}`, data).then(res => {
	  				if(res.code == 0) {
	  					this.prod = res.data;
						this.handleCommentTag(this.prod.productTags);
	  				}else {
	  					this.$message({
	  						text: res.msg,
	  						type: 'error'
	  					})
	  				}
	  			}).catch(err => {
	  				this.$message({
  						text: '网络错误',
  						type: 'warn'
  					})
	  			})
			},
			handleCommentTag(tagList) {
				let arr = this.prod.productTags.sort((item1, item2) => {
					return item2.commentNum - item1.commentNum;
				})
				this.hasGrade = this.prod.userComments.filter((item, index) => {
					return item.type == 1;
				}).length > 0;
				let selectTagList = this.prod.userComments.filter((item, index) => {
					return item.type == 2;
				})
				let selectLoanList = this.prod.userComments.filter((item, index) => {
					return item.type == 3 || item.type == 4;
				});
				this.isSelectLoan = selectLoanList.length > 0;
				if(selectLoanList.length > 0) {
					this.selectType = selectLoanList[0].type;
				}
				let length1 = arr.length;
				let length2 = selectTagList.length;
				for(let i = 0; i < length1; i++) {
					for(let j = 0; j < length2; j++) {
						if(arr[i].tagId == selectTagList[j].tagId) {
							arr[i].selected = true
						}
					}
				}
				this.goodList = [];
				this.mediumList = [];
				this.badList = [];
				let length = arr.length;
				for(let i = 0; i < length; i++) {
					if(i < 3) {
						this.goodList.push(arr[i])
					}else if(i >= 3 && i < 7) {
						this.mediumList.push(arr[i])
					}else {
						this.badList.push(arr[i])
					}
				}
				console.log(this.goodList);
			},
			selectIcon(item) {
				let img = ''
				if(item.selected) {
					if(item.tagType == 1) {
						img = require('../assets/img/ic_rate_positive_w.png')
					}else if(item.tagType == 2) {
						img = require('../assets/img/ic_rate_neutral_w.png')
					}else {
						img = require('../assets/img/ic_rate_negative_w.png')
					}
				}else {
					if(item.tagType == 1) {
						img = require('../assets/img/ic_rate_positive.png')
					}else if(item.tagType == 2) {
						img = require('../assets/img/ic_rate_neutral.png')
					}else {
						img = require('../assets/img/ic_rate_negative.png')
					}
				}
				return img;
			},
			hendleClickTag(listName, item, index) {
				if(item.selected) {
					this[listName][index].commentNum -= 1;
				}else {
					sa.track('st_click', {
						stName: item.tagName,
						loanname: this.prod.platformName
					})
					this[listName][index].commentNum += 1;
				}
				this[listName][index].selected = !item.selected;
				this[listName] = JSON.parse(JSON.stringify(this[listName]));
				this.submitComment(2, item);
			},
			submitComment(type, item) {
				let data = {
					anonymousDevice: localStorage.getItem('randomId'),
					productId: this.prod.id,
					type,
				}
				if(type == 1) {
					data.tagId = this.gradeSum;
				}else if(type == 2) {
					data.tagId = item.tagId;
				}
				api.requestNews(`headline-product/mark`, data).then(res => {
	  				if(res.code == 0) {
	  					if(type == 1) {
	  						sa.track('conGrade_success', {
	  							loanname: this.prod.platformName
	  						});
	  					}
	  					this.getDetail();
	  				}else {
	  					this.$message({
	  						text: res.msg,
	  						type: 'error'
	  					})
	  				}
	  			})
			},
			joinQQ() {
				if(this.appClient_) {
		    		this.appClient_.joinQQGroup('J0T25GOveGR3shAZ1OiH8b2Uny5hpMex');
		    	}else {
		    		location.href = 'http://qm.qq.com/cgi-bin/qm/qr?k=y4gY5GxPKpMDPfBrxIxLg7GOgR5GJ7h0';
		    	}
			},
			handleClickMask(e) {
				if(e.target.id == 'loan_comment_mask') {
					this.isComment = false;
				}
			}
		},
		created() {
	  		sa.registerPage({
				'platform': localStorage.getItem('platform') || 'test',
				'origin': localStorage.getItem('origin') || 'test',
			});
			this.getDetail()
		},
		mounted() {
			document.title = '平台详情'
		}
	}
</script>

