
<template>
	<div class="container">
		<div class="head">
	    	<img class="teacher_logo" src="../../assets/img/course_cover_sq.png">
		    <div class="class_title_box">
		    	<div class="class_title_name">第{{order}}课</div>
		    	<div class="class_title_guide">{{currentClassInfo.courseName}}</div>
		    	<div class="class_title_info">
		    		<div class="class_title_num">{{185 + currentClassInfo.playNum * 13}}人正在学习</div>
		    	</div>
		    </div>
	    </div>
	    <div id="play_box" class="play_box" :class="{fixedTab: isFixedTab}">
		    <audio id="audio" class="play_audio" controls preload="auto" ref="audio">
				<source :src="currentClassInfo.courseUrl" type="audio/mpeg">
				您的浏览器不支持 audio 元素。
			</audio>
	    	<div class="play">
    			<ProgressCircle :radius="0.48 * rem" :percent="percent">
					<img class="play_btn" :src="playImg" @click="handleClickPlay">
				</ProgressCircle>
	    		<div class="play_intro">
	    			<div class="play_intro_title">{{playText}}</div>
	    			<div class="play_intro_text">
	    				<span style="color: #F0532D">{{$refs.audio ? currentTime : '00:00'}}</span>
	    				<span>/</span>
	    				<span style="margin-right: 0.08rem">{{this.duration}}</span>
	    			</div>
	    		</div>
    			<div class="list_btn" @click="showCommentList = true">
    				<img class="list_btn_icon" src="../../assets/img/classroom3/play_comments.png">
    				<div class="list_btn_text">大家都在说</div>
    			</div>
    			<div class="line2"></div>
    			<div class="list_btn" @click="showClassList = true">
    				<img class="list_btn_icon" src="../../assets/img/classroom3/play_list.png">
    				<div class="list_btn_text">课程列表</div>
    			</div>
    			<div class="loan_num_range" v-show="this.percent > 0">
		          	<div class="loan_num_range_btn" id="loan_num_range_btn">
		            	<div class="loan_num_range_icon" @touchmove="handleTouchmove"></div>
		          	</div>
		          	<div class="loan_num_range_basic">
		            	<div class="loan_num_range_twig" :style="{width: percent * 100 + '%'}"></div>
		          	</div>
		        </div>
	    	</div>
	    </div>
	    <!-- <div class="class_guide_check" v-if="!isPay" :class="{margin80: isFixedTab}" @click="checkOutCounter">
	    	<img class="class_guide_check_head" src="../../assets/img/classroom3/banner_headimg.png" alt="">
			<div class="class_guide_check_content">
				<div class="class_guide_check_title">老白说贷款</div>
				<div class="class_guide_check_text">避免踩坑，手把手教你下款</div>
			</div>
			<div class="class_guide_check_box">
				<div class="class_guide_check_num">{{studyNum}}人已购买</div>
				<div class="class_guide_check_btn">订购课程</div>
			</div>
	    </div> -->
	    <div class="news_info" :style="{padding: 0}" @click="apply">
			<div class="loan_comment" style="color: #fff; background: #FF5D50;">限时放款</div>
			<div class="loan" v-if="ad.adId && ad.adContent">
				<div class="loan_name">
					<img class="loan_logo" :src="ad.adImg">
					<div class="loan_name_text">{{ad.adName}}</div>
				</div>
				<div class="loan_info">
					<div class="loan_limit"><span>¥</span>{{limitList[parseInt(adIndex % limitList.length)]}}</div>
					<div class="loan_intro">{{ad.adContent}}</div>
				</div>
				<div class="loan_btn">马上申请</div>
			</div>
			<img :src="ad.adImg" class="ad_img" v-else/>
		</div>
	    <div class="class_keynote">
	    	<div class="class_keynote_title">本课重点</div>
		    <div class="joinQQ">
				<div class="joinQQ_guide">如果你还有问题，我在群里等你</div>
				<div class="joinQQ_btn" @click="joinQQ">加群</div>
			</div>
	    	<div class="class_keynote_list" v-html="currentClassInfo.point"></div>
	    	<!-- <div class="class_keynote_list">
	    		<br/>
	    	</div> -->
	    </div>
	    <!-- <div class="class_text" v-html="classText[currentClassInfo.proCode.split('-')[1] - 1].classText">
	    	
	    </div> -->
    	<div class="class_num">
    		听课
    		<span style="color: #07667A; flex: 1; margin-left: 0.04rem"><span v-show="showNum">{{185 + currentClassInfo.playNum * 13}}</span>人</span>
    		<img class="class_num_icon" :src="isUseful ? require('../../assets/img/classroom3/course_helpful.png') : require('../../assets/img/classroom3/course_helpful_gray.png')" @click="helpful">
    		有用
    		<span style="color: #F0532D; margin-left: 0.04rem" @click="helpful"><span v-show="showNum">{{96 + currentClassInfo.usefulNum * 8 + (isUseful ? 1 : 0)}}</span>人</span>
    	</div>
		<action-sheet
			:show='showClassList'
			title="课程列表"
        	@close="showClassList = false"
		>
			<div class="class_list" @touchmove="onTouchMove">
				<div class="class_item" v-for="(item, index) in classList" :key="item.classCourseId" @click="check(item, classList.length - index)">
					<div class="class_item_order">{{classList.length - index}}</div>
					<div class="class_item_title" :class="{class_grounding: item.status == 2}">[ 第{{classList.length - index}}课 ] : {{item.courseName}}</div>
				</div>
			</div>	
		</action-sheet>
		<action-sheet
			:show='showCommentList'
			title="大家都在说"
        	@close="showCommentList = false"
		>
			<div class="comment_list" @touchmove="onTouchMove">
				<div class="comment_item" v-for="(item, index) in commentList" :key="item.nickname">
					<img class="comment_item_head" :src="item.headUrl">
					<div class="comment_item_content">
						<div class="comment_item_info">
							<div class="comment_item_name">{{item.nickname}}</div>
							<!-- <img class="comment_item_vip" src="../../assets/img/classroom3/tag_paidmember.png" v-if="item.isPaidUser"> -->
						</div>
						<div class="comment_item_text">{{item.content}}</div>
					</div>
				</div>
			</div>	
		</action-sheet>
		<div class="tip" v-show="showPayTip">
			<div class="tip_content">
				<div class="tip_text">您已支付成功，所有课程免费畅听。</div>
				<div class="tip_close" @click="showPayTip = false">去听课</div>
			</div>
		</div>
		<pay v-if="isCheck" :isCheck="isCheck" :proAmount="proAmount" @pay="pay" @close="isCheck = false"></pay>
    	<div class="form" id="form"></div>
	</div>
</template>

<script>
  	import api from '../../api/classroom'
	import {AppClient, setupWebViewJavascriptBridge} from '../../utils/Bridge'
	import { formatTime } from '../../utils/utils'
  	import { isIOS, isAndroid, getScript, callbackUrl, requestLogin, jumpUrl, isWechat, jsonSort } from '../../api/classroom'
  	import md5 from 'js-md5'
	import ProgressCircle from '../../components/circle1'
	import classText from '../../data/classText'
	import Pay from '../components/pay'
	import ActionSheet from '../components/actionSheet'
	export default {
		data() {
			return {
				appClient_: null,
				percent: 0,
				isPlay: false,
				isFixedTab: false,
				timer: null,
				studyNum: localStorage.getItem('studyNum'),
				helpNum: 0,
				addNum: [220, 215, 209, 225, 211, 252, 243, 200, 208, 233],
				playImg: require('../../assets/img/classroom2/course_play.png'), // 播放icon
				currentClassInfo: JSON.parse(sessionStorage.getItem('currentClassInfo')), // 当前课程信息
				order: sessionStorage.getItem('order'), // 课程顺序
				classText,	// 课程文案
				playText: '点击播放', // 播放状态文案
				duration: '00:00', // 播放总时长
				canplay: false, // 是否达到可以播放
				currentTime: '00:00', //当前展示展示播放时间
				proAmount: sessionStorage.getItem('proAmount'),
				isCheck: false,
				showClassList: false, // 课程列表是否展示
				classList: [], // 课程列表
				showCommentList: false, // 评论列表是否展示
				commentList: [], // 评论列表
				orderInfo: {},
				isPay: localStorage.getItem('isPay') == 1,
				showPayTip: false,
				num: '',
				isUseful: false,
				showNum: false,
				duration_: 0,
	    		limitList: [10000, 40000, 20000, 60000, 200000, 30000, 50000, 100000],
	    		ad: {},
	    		adIndex: 0
			}
		},
		components: {
			ProgressCircle,
	  		Pay,
	  		ActionSheet
		},
		methods: {
			appClient() {
		      const self = this;
		      if (window.android) {
		        this.appClient_ = new AppClient(window.android, true);
		        this.init();
		      } else {
		        setupWebViewJavascriptBridge((bridge) => {
		          this.appClient_ = new AppClient(bridge, false);
		          self.init();
		        });
		      }
		    },

		    init() {
		        let self = this;
		        window.initDataCallback=(data) =>{
		        	this.appData = JSON.parse(data);
		        }
		        this.appClient_.getSignInfo("initDataCallback",initDataCallback,{});
		    	console.log(this.appClient_.getSignInfo);
		    	console.log(this.appClient_.joinQQGroup);
		    	console.log(this.appClient_);

		    },
	      	fixedTab() {
		        let self = this;
		        $(window).scroll(() => {
		          	if($(window).scrollTop() > this.tabOffsetTop) {
		            	self.isFixedTab = true;
		          	}else {
		            	self.isFixedTab = false;
		          	}
		        });
		    },
		    getOrder() {
		    	let obj = {
		    		randomId: localStorage.getItem('randomId'),
		    		courseSource: 2
		    	}
		        api.requestClass('class/course/getList', JSON.stringify(obj), 'application/json; charset=utf-8').then(res => {
		        	if(res.code == 0) {
		        		this.classList = res.data.courseList.reverse().filter(item => item.status == 2);
		        		this.proAmount = res.data.proAmount;
		        		this.isPay = res.data.havePaid;
		        		if(localStorage.getItem('isPay') != 1 && this.isPay) {
		        			this.showPayTip = true
		        		}
		        		localStorage.setItem('isPay', res.data.havePaid ? 1 : 2);
		        	}else {
		        		this.$message({
		        			text: res.msg,
		        			type: 'warn'
		        		})
		        	}
		        })
		    },
	  		getAd() {
	  			api.requestGet('headline/ads', {locate: 'xiaobaizixun'}).then(res => {
	  				if(res.code == 0) {
	  					this.adIndex = parseInt(localStorage.getItem('adIndex')) + 1;
	  					localStorage.setItem('adIndex', this.adIndex % res.data.length);
	  					this.ad = res.data[this.adIndex % res.data.length];
	  					this.ad.limit = this.limitList[this.adIndex % this.limitList.length];
	  				}else {
	  					this.$message({
	  						text: res.msg,
	  						type: 'error'
	  					})
	  				}
	  			})
	  		},
		    getComment() {
				api.requestGet('/class/course/advice/list', {courseSource: 2}, 'application/json; charset=utf-8').then(res => {
		        	if(res.code == 0) {
		        		this.commentList = res.data;
		        	}else {
		        		this.$message({
		        			text: res.msg,
		        			type: 'warn'
		        		})
		        	}
		        })
		    },
		    goBlackCheck() {
		    	sa.track('payCheck_click', {
		    		activename: 'creditCourse',
	  				activepagename: 'classDetail',
		  			'$utm_medium': localStorage.getItem('utm_medium'),
		  			'$utm_source': localStorage.getItem('utm_source')
		    	}, () => {
		    		location.href = 'https://sensors-api.kongapi.com/t/C8';
		    	});
		    },
		    onTouchMove(e) {
			    // e.preventDefault()
			    e.stopPropagation();
			    // window.event ? window.event.cancelBubble = true : e.stopPropagation();
			},
		    handleTouchmove(e) {
		        e = e || window.event;
		        let left = e.changedTouches[0].clientX - ($(window).width() - $('.loan_num_range').width()) / 2 - $('#loan_num_range_btn').width() / 2;
		        if(left < -6) {
		          	left = -6;
		        }
		        if(left > window.innerWidth - 6) {
		          	left = window.innerWidth - 6;
		        }
		        $('#loan_num_range_btn').css({left});
		        this.$refs.audio.currentTime = (left + 6) / window.innerWidth * this.$refs.audio.duration;
		        this.percent = this.$refs.audio.currentTime / this.$refs.audio.duration;
		        this.currentTime = formatTime(this.$refs.audio.currentTime * 1000, 'mm:ss');
	      	},
		    handleClickPlay() {
		    	if(this.currentClassInfo.courseType == 'free' || localStorage.getItem('isPay') == 1) {
		    		if(this.isPlay) {
			    		this.$refs.audio.pause();
			    		this.playImg = require('../../assets/img/classroom2/course_play.png');
			    		clearInterval(this.timer);
			    	}else {
			    		if(this.percent == 1) {
			    			this.percent = 0;
			        		$('#loan_num_range_btn').css({left: 0});
			    		}
			    		this.play();
			    		this.$refs.audio.play();
			    		this.timer = setInterval(() => {
			    			this.percent = this.$refs.audio.currentTime / this.$refs.audio.duration;
			    			if(this.$refs.audio.currentTime == 0) {
			    				this.playImg = require('../../assets/img/loading.gif');
			    			}else {
			    				if(this.$refs.audio.currentTime >= this.$refs.audio.duration) {
				    				this.percent = 1;
				    				this.isPlay = false;
				    				this.playImg = require('../../assets/img/classroom2/course_play.png');
				    				clearInterval(this.timer);
				    			}else {
				    				this.playImg = require('../../assets/img/classroom2/course_pause.png');
				    			}
			        			$('#loan_num_range_btn').css({left: this.percent * window.innerWidth - 6 + 'px'});
			    			}
			        		this.currentTime = formatTime(this.$refs.audio.currentTime * 1000, 'mm:ss');
			    		}, 1000)
			    	}
			    	this.isPlay = !this.isPlay
		    	}else {
		    		this.checkOutCounter();
		    	}
		    },
		    play() {
		    	let obj = {
		          	classCourseId: this.currentClassInfo.classCourseId
		        }
		        api.requestClass('class/course/update/play', JSON.stringify(obj), 'application/json; charset=utf-8')
		    },
		    getUseful() {
		    	let obj = {
		          	randomId: localStorage.getItem('randomId'),
		          	classCourseId: this.currentClassInfo.classCourseId
		        }
		        api.requestClass('class/course/isUseful', JSON.stringify(obj), 'application/json; charset=utf-8').then(res => {
		        	if(res.code == 0) {
		        		this.isUseful = res.data.isUseful;
		        		this.showNum = true;
		        	}else {
		        		this.$message({
		        			text: res.msg,
		        			type: 'warn'
		        		})
		        	}
		        })
		    },
		    helpful() {
				let obj = {
					randomId: localStorage.getItem('randomId'),
		          	classCourseId: this.currentClassInfo.classCourseId
		        }
		        api.requestClass('/class/course/update/useful', JSON.stringify(obj), 'application/json; charset=utf-8').then(res => {
		        	this.getUseful()
		        })
		    },
		    check(item, order) {
		    	this.showClassList = false;
		    	sessionStorage.setItem('currentClassInfo', JSON.stringify(item));
		    	sessionStorage.setItem('order', order);
		    	this.$router.go(0);
		    	// if(!this.isPay && item.courseType != 'free') {
		    	// 	this.checkOutCounter();
		    	// }else {
		    	// 	this.$router.go(0);
		    	// }
		    },
		    checkOutCounter() {
		    	sa.track('clsssSubscribe_click', {
		  			activename: 'creditCourse',
	  				activepagename: 'classDetail',
	  				classType: this.currentClassInfo.courseType,
  					className: `第${this.order}课`,
		  			'$utm_medium': localStorage.getItem('utm_medium'),
		  			'$utm_source': localStorage.getItem('utm_source')
		  		})
		  		let obj = {
		          	proCode: 'course',
		          	randomId: localStorage.getItem('randomId')
		        }
		        api.requestClass('orderClass/checkoutCounterV5', JSON.stringify(obj), 'application/json; charset=utf-8').then(res => {
		        	if(res.code == 0) {
		        		this.orderInfo = res.data;
		        		this.isCheck = true;
		        	}else {
		        		this.$message({
		        			text: res.msg,
		        			type: 'warn'
		        		})
		        	}
		        })
		    },
		    pay() {
		    	sa.track('payCheck_click', {
		    		activename: 'creditCourse',
	  				activepagename: 'classDetail',
	  				classType: this.currentClassInfo.courseType,
  					className: `第${this.order}课`,
		  			'$utm_medium': localStorage.getItem('utm_medium'),
		  			'$utm_source': localStorage.getItem('utm_source')
		    	}, () => {
		    		if (isWechat()) {
		              // 微信收银台支付
		              let data = {
		                'callback_url': `${jumpUrl}guide`,
		                'mchid': '1527581321',
		                'notify_url': `${callbackUrl}orderClass/wechat/payjs/classroom/callback`,
		                'out_trade_no': this.orderInfo.orderCode,
		                'total_fee': parseInt(this.orderInfo.orderRealAmount)
		              }
		              let stringA = `callback_url=${data.callback_url}&mchid=${data.mchid}&notify_url=${data.notify_url}&out_trade_no=${data.out_trade_no}&total_fee=${data.total_fee}&key=PDXOdDsQVBx9M8p6`
		              data.sign = md5(stringA).toUpperCase()
		              window.location.href = `https://payjs.cn/api/cashier?callback_url=${data.callback_url}&mchid=${data.mchid}&notify_url=${data.notify_url}&total_fee=${data.total_fee}&out_trade_no=${data.out_trade_no}&sign=${data.sign}`
		            } else {
		              // 阿里支付
		              let data = {
		                payWay: 'ALIPAY',
		                orderCode: this.orderInfo.orderCode,
		                returnUrl: location.href,
		                deviceType: 'wap'
		              }
		              api.requestClass('orderClass/getPayCode', data)
		                .then(res => {
		                  if(res.code == 0) {
		                    $('#form').html(res.data);
		                  }else {
		                  	this.$message({
		                  		text: res.msg,
		                  		type: 'warn'
		                  	})
		                  }
		                }).catch(err => {
		                console.log(err)
		              })
		            }
		    	})
	        },
		    formatTime(value, type) {
		    	return formatTime(value, type);
		    },
		    joinQQ() {
		    	sa.track('attendGroup_click', {
					activename: 'creditCourse',
		  			activepagename: 'classDetail',
		  			'$utm_medium': localStorage.getItem('utm_medium'),
		  			'$utm_source': localStorage.getItem('utm_source')
				}, () => {
					if(this.appClient_ && this.appClient_.joinQQGroup) {
			    		this.appClient_.joinQQGroup('y4gY5GxPKpMDPfBrxIxLg7GOgR5GJ7h0');
			    	}else {
			    		location.href = 'http://qm.qq.com/cgi-bin/qm/qr?k=y4gY5GxPKpMDPfBrxIxLg7GOgR5GJ7h0';
			    	}
				})
			},
			getHelp() {
				if(!localStorage.getItem('isHelp')) {
					this.currentClassInfo.helpNum++;

					localStorage.setItem('isHelp', 1);
				}
			},
			handleModel(val) {
				if(val) {
					document.body.style.overflow = 'hidden';
				}else {
					document.body.style.overflow = 'scroll';
				}
			},
			handleNum() {
				let day = parseInt(new Date().getTime() / 1000 / 60 / 60 / 24)
				let initDay = 18016;
				if(day <= initDay) {
					this.num = 720;
					localStorage.setItem('num', 166);
				}else {
					if(parseInt(localStorage.getItem('day')) != day) {
						let length = day - initDay;
						let index = 0;
						this.num = 166;
						for(var i = 0; i < length; i++) {
							index = i % 10;
							this.num += this.addNum[index];
						}
						localStorage.setItem('num', this.num);
						localStorage.setItem('day', day);
					}else {
						this.num = localStorage.getItem('num');
					}
				}
			},
			trackListenTime(newTime) {
				sa.track('gp_click', {
					activename: 'creditCourse',
		  			activepagename: 'classDetail',
	  				classType: this.currentClassInfo.courseType,
	  				className: `第${this.order}课`,
		  			'$utm_medium': localStorage.getItem('utm_medium'),
		  			'$utm_source': localStorage.getItem('utm_source'),
		  			listenTime: newTime
				})
			},
		    apply() {
		    	sa.track('unionAd_click', {
		    		adName: 'courseMid'
		    	})
		    	setTimeout(() => {
		    		location.href = this.ad.adUrl
		    	}, 300)
		    	
		    }
		},
		watch: {
			'showClassList': 'handleModel',
			'showCommentList': 'handleModel',
			percent(newValue, oldValue) {
				let newTime = newValue * this.duration_;
				let oldTime = oldValue * this.duration_;
				console.log(newTime, oldTime);
				if(newTime > 0 && oldTime == 0) {
					this.trackListenTime(0)
				}else if(newTime >= 10 && oldTime < 10) {
					this.trackListenTime(10)
				}else if(newTime >= 30 && oldTime < 30) {
					this.trackListenTime(30)
				}else if(newTime >= 60 && oldTime < 60) {
					this.trackListenTime(60)
				}else if(newTime >= 90 && oldTime < 90) {
					this.trackListenTime(90)
				}else if(newTime >= 180 && oldTime < 180) {
					this.trackListenTime(180)
				}
			}
		},
		computed: {
			rem() {
				return window.innerWidth / 3.6;
			},
			width() {
				return window.innerWidth * this.percent;
			}
		},
		created() {
			this.appClient();
			this.getOrder();
			this.getAd();
			this.getComment();
			this.handleNum();
	  		this.getUseful();
			if(localStorage.getItem('isHelp')) {
				this.currentClassInfo.helpNum++;
			}
			window.scrollTo(0,0);
		},
		mounted() {
			document.title = '老白课堂'
			sa.track('webview', {
	  			activename: 'creditCourse',
	  			activepagename: 'classDetail',
  				classType: this.currentClassInfo.courseType,
  				className: `第${this.order}课`,
	  			'$utm_medium': localStorage.getItem('utm_medium'),
	  			'$utm_source': localStorage.getItem('utm_source')
	  		})
			this.$nextTick(() => {
				this.tabOffsetTop = $('#play_box')[0].offsetTop;
				this.fixedTab();
				let self = this;
				$('audio').on('canplay',function(){
					self.duration_ = self.$refs.audio.duration;
			    	self.duration = self.formatTime(self.$refs.audio.duration * 1000, 'mm:ss');
			    })
			})
		}
	}
</script>

