<template>
	<div class="check_box" ref="check_box">
		<div class="check_content">
			<div class="check_detail">
				<div class="tip_title">购买课程</div>
				<div class="class_intro_title">
					<div class="class_intro_title_text">课程亮点</div>
					<div class="line1"></div>
				</div>
				<div class="class_service">
					<div class="class_service_item">
						<img class="class_service_item_icon" src="../../assets/img/classroom3/bounus_useful.png">
						<span class="class_service_item_name">课程专业有效</span>
					</div>
					<div class="class_service_item">
						<img class="class_service_item_icon" src="../../assets/img/classroom3/bonus_update.png">
						<span class="class_service_item_name">内容持续更新</span>
					</div>
					<div class="class_service_item">
						<img class="class_service_item_icon" src="../../assets/img/classroom3/bonus_unlimited.png">
						<span class="class_service_item_name">所有课程畅听</span>
					</div>
					<div class="class_service_item">
						<img class="class_service_item_icon" src="../../assets/img/classroom3/bounus_refund.png">
						<span class="class_service_item_name">不满意包退款</span>
					</div>
				</div>
				<!-- <div class="class_rules_list">
					1. 课程支付后不予退款，请确认后购买课程。<br/>
					2. 本课程未经授权，不得用于其他商业用途。
				</div> -->
			</div>
			<div class="check_btn" @click="$emit('pay')">确认支付{{proAmount / 100}}元</div>
			<img class="tip_close_btn" src="../../assets/img/classroom3/close.png" @click="$emit('close')">
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			isCheck: {
				type: Boolean,
				default: false
			},
			proAmount: {
				type: Number,
				default: 0
			}
		},
		methods: {
			onTouchMove(e) {
			    e.preventDefault()
			    // e.stopPropagation()
			    // window.event ? window.event.cancelBubble = true : e.stopPropagation();
			}
		},
		mounted() {
			// this.$refs.check_box.addEventListener('touchmove', this.onTouchMove, false);
		}
	}
</script>

<style scoped>
	
	.check_box {
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 1000;
		background: rgba(0,0,0,0.38);
	}
	.check_content {
		position: absolute;
		bottom: 0;
		width: 100%;
		background: #fff;
	}
	.check_detail {
		box-sizing: border-box;
		padding: 0.24rem;
		width: 100%;
	}
	.check_btn {
		margin: 0 auto 0.16rem;
		width: 3.28rem;
		height: 0.4rem;
		background-image: linear-gradient(270deg, #FF5D50 0%, #F37121 100%);
		border-radius: 4px;
		font-family: PingFangSC-Medium;
		font-size: 0.15rem;
		color: #FFFFFF;
		text-align: center;
		line-height: 0.4rem;
	}
	.tip_close_btn {
		position: absolute;
		width: 0.24rem;
		height: 0.24rem;
		top: 0.24rem;
		right: 0.16rem;
	}
	.tip_title {
		font-family: PingFangSC-Medium;
		font-size: 0.2rem;
		color: rgba(0,0,0,0.87);
		line-height: 0.2rem;
	}
	.class_intro_title {
		margin-top: 0.16rem;
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	.class_intro_title_text {
		font-family: PingFangSC-Medium;
		font-size: 0.16rem;
		color: rgba(0,0,0,0.60);
		text-align: center;
		line-height: 0.22rem;
	}
	.line1 {
		margin-left: 0.16rem;
		flex: 1;
		border: 0.01rem solid rgba(0,0,0,0.12);
	}
	.class_service {
		margin: 0 auto;
		margin-top: 0.16rem;
		border-bottom: 0.01rem solid rgba(0,0,0,0.12);
		padding-bottom: 0;
		box-sizing: border-box;
		width: 3.28rem;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: wrap;
	}
	.class_service_item {
		margin-bottom: 0.24rem;
		width: 50%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: nowrap;
	}
	/*.class_service_item:nth-child(2n+1) {
		margin-right: 0.36rem;
	}*/
	.class_service_item_icon {
		width: 0.4rem;
		height: 0.4rem;
	}
	.class_service_item_name {
		margin-left: 0.08rem;
		font-family: PingFangSC-Medium;
		font-size: 0.14rem;
		color: rgba(0,0,0,0.60);
		text-align: left;
		line-height: 0.2rem;
	}
	.class_rules_list {
		margin-top: 0.16rem;
		font-family: PingFangSC-Regular;
		font-size: 0.13rem;
		color: rgba(0,0,0,0.60);
		text-align: left;
		line-height: 0.26rem;
	}
</style>


