<template>
	<div class="content">
		<div class="news_list">
			<div class="news_item" v-for="(item, index) in list" :key="index" @click="handleClick(item)">
				<div class="news_info" v-if="item.id">
					<div class="news_title">{{item.title}}</div>
					<div class="news_text">
						<img class="news_read_icon" src="../../assets/img/ic_news_viewed.png">
						<div class="news_read_num">{{item.uvNum}}次浏览</div>
						<div class="news_time">{{`${new Date(item.articleTime).getFullYear()}/${new Date(item.articleTime).getMonth() + 1}/${new Date(item.articleTime).getDate()}`}}</div>
					</div>
				</div>
				<img class="news_banner" :src="item.historyBannerUrl" v-if="item.id">
				<div class="news_info" v-if="item.adId && item.adContent" :style="{padding: 0}">
					<!-- <div class="news_title">{{item.adName}}</div>
					<div class="news_text">
						<div class="news_read_num">广告</div>
						<div class="news_time">{{`${new Date(item.createTime).getFullYear()}/${new Date(item.createTime).getMonth() + 1}/${new Date(item.createTime).getDate()}`}}</div>
					</div> -->
					<!-- <div class="loan_box" @click="apply">
						
					</div> -->
					<div class="loan_comment" style="color: #fff; background: #FF5D50;">限时放款</div>
					<div class="loan">
						<div class="loan_name">
							<img class="loan_logo" :src="item.adImg">
							<div class="loan_name_text">{{item.adName}}</div>
						</div>
						<div class="loan_info">
							<div class="loan_limit"><span>¥</span>{{limitList[parseInt(index % limitList.length)]}}</div>
							<div class="loan_intro">{{item.adContent}}</div>
						</div>
						<div class="loan_btn">马上申请</div>
					</div>
				</div>
				<img :src="item.adImg" class="ad_img" v-if="item.adId && !item.adContent"/>
			</div>
			<!-- <div class="news_item" id="guide_intro" style="height: 1rem;" v-if="ad.type == 1" @click="handleAdClick">
				<div class="news_info">
					<div class="news_title">{{ad.name}}</div>
					<div class="news_text">
						<div class="news_read_num">广告</div>
						<div class="news_time">{{`${new Date(ad.gmtCreated).getFullYear()}/${new Date(ad.gmtCreated).getMonth() + 1}/${new Date(ad.gmtCreated).getDate()}`}}</div>
					</div>
				</div>
				<img class="news_banner" :src="ad.bannerUrl">
			</div>
			<img :src="ad.bannerUrl" class="ad_img" v-if="ad.type == 2" @click="handleAdClick"/>
			<div v-for="(item, index) in newsList" :key="index" @click="handleArticleClick(item)" v-if="index >= 2">
				<div class="news_item">
					<div class="news_info">
						<div class="news_title">{{item.title}}</div>
						<div class="news_text">
							<img class="news_read_icon" src="../../assets/img/ic_news_viewed.png">
							<div class="news_read_num">{{item.uvNum}}次浏览</div>
							<div class="news_time">{{`${new Date(item.articleTime).getFullYear()}/${new Date(item.articleTime).getMonth() + 1}/${new Date(item.articleTime).getDate()}`}}</div>
						</div>
					</div>
					<img class="news_banner" :src="item.historyBannerUrl">
				</div>
			</div> -->
		</div>
		<div class="bottom_tip">已经到底啦</div>
	</div>
</template>

<script>
  	import api from '../../api/classroom'
	export default {
	  	name: 'index',
	  	components: {
	  		
	  	},
	  	data () {
	    	return {
	    		newsList: [],
	    		adList: {},
	    		limitList: [10000, 40000, 20000, 60000, 200000, 30000, 50000, 100000]
		    }
	  	},
	  	computed: {
	  		list() {
	  			let newsLength = this.newsList.length;
	  			let adLength = this.adList.length;
	  			let list = [];
	  			let adIndex = localStorage.getItem('adIndex') ? parseInt(localStorage.getItem('adIndex')) + 1 : 0;
	  			for(let i = 0; i < newsLength; i++) {
	  				list.push(this.newsList[i]);
	  				if(i % 3 == 1 && this.adList[(parseInt(i / 3) + adIndex) % adLength]) {
	  					localStorage.setItem('adIndex', (parseInt(i / 3) + adIndex) % adLength);
	  					list.push(this.adList[(parseInt(i / 3) + adIndex) % adLength]);
	  				}
	  			}
	  			return list;
	  		}
	  	},
	  	methods: {
	  		getNews() {
	  			api.requestGet('headline/articles', {}).then(res => {
	  				if(res.code == 0) {
	  					let newsList = res.data.sort((item1, item2) => {
	  						return item2.articleTime - item1.articleTime
	  					})
	  					let length = newsList.length;
	  					for(var i = 0; i < length; i++) {
	  						if(newsList[i].title.length > 23) {
	  							newsList[i].title = newsList[i].title.slice(0, 23) + '...';
	  						}
	  					}
	  					this.newsList = newsList;
		        		this.$emit('hide');
	  				}else {
	  					this.$message({
	  						text: res.msg,
	  						type: 'error'
	  					})
	  				}
	  			})
	  		},
	  		getAd() {
	  			api.requestGet('headline/ads', {locate: 'xiaobaizixun'}).then(res => {
	  				if(res.code == 0) {
	  					this.adList = res.data;
	  				}else {
	  					this.$message({
	  						text: res.msg,
	  						type: 'error'
	  					})
	  				}
	  			})
	  		},
	  		handleClick(item) {
	  			item.id ? this.handleArticleClick(item) : this.handleAdClick(item)
	  		},
	  		handleArticleClick(item) {
	  			sa.track('hotP_click');
	  			sa.track('cot_click', {
	  				cotList: item.title
	  			})
	  			api.requestGet(`/headline/view/${item.id}`, {}).then(res => {
	  				setTimeout(() => {
		  				location.href = `${item.articleUrl}&isNews=1&platform=${localStorage.getItem('platform')}&origin=${localStorage.getItem('origin')}`;
		  			}, 300)
	  			})
	  		},
	  		handleAdClick(item) {
	  			sa.track('hotP_click');
	  			sa.track('newsAd_click', {
	  				adUrl: item.adUrl
	  			})
	  			sa.track('unionAd_click', {
	  				adName: 'cotlist'
	  			})
	  			setTimeout(() => {
	  				location.href = item.adUrl;
	  			}, 300)
	  		},
	  	},
	  	created() {
	  		sa.registerPage({
				activepagename: 'hotnewsPage',
				h5TabName: 'kuaibaoTab'
			});
			sa.track('webview');
			sa.track('hotnewsPage');
	  		this.getNews();
	  		this.getAd();
			window.scrollTo(0,0);
	  	},
	  	mounted() {
	  		
	 	},
	}
</script>

<style scoped>
	.news_list {
		padding: 0 0.16rem;
		box-sizing: border-box;
		width: 100%;
	}
	.news_item {
		position: relative;
		margin-bottom: 0.08rem;
		width: 100%;
		height: 0.94rem;
		background: #FFFFFF;
		box-shadow: 0 0.04rem 0.16rem 0 rgba(0,0,0,0.04);
		border-radius: 0.04rem;
		display: flex;
		/*overflow: hidden;*/
	}
	.news_info {
		flex: 1;
		padding: 0 0.16rem;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.news_title {
		margin-top: 0.16rem;
		font-family: PingFangSC-Regular;
		font-size: 0.16rem;
		color: rgba(0,0,0,0.87);
		text-align: left;
		line-height: 0.22rem;
	}
	.news_text {
		margin-bottom: 0.14rem;
		display: flex;
		font-family: PingFangSC-Regular;
		font-size: 0.12rem;
		color: rgba(0,0,0,0.26);
		line-height: 0.18rem;
	}
	.news_read_icon {
		width: 0.16rem;
		height: 0.16rem;
	}
	.news_read_num {
		margin-left: 0.04rem;
		flex: 1;
	}
	.news_banner {
		border: none;
		margin: 0.12rem 0.15rem;
		width: 0.7rem;
		height: 0.7rem;
		flex-shrink: 1;
		background: url('../../assets/img/img_default_preloadingimg.png') top no-repeat;
		background-size: 100%;
		border-radius: 0.04rem;
	}
	.advise {
		width: 100%;
		background: #FFFFFF;
		box-shadow: 0 0.04rem 0.16rem 0 rgba(0,0,0,0.04);
		border-radius: 0.04rem;
	}
	.advise_title {
		padding: 0.16rem;
		padding-bottom: 0.12rem;
		font-family: PingFangSC-Regular;
		font-size: 0.16rem;
		color: rgba(0,0,0,0.87);
		text-align: left;
		line-height: 0.22rem;
	}
	.advise_banner {
		width: 100%;
		height: 1rem;
	}
	.bottom_tip {
		margin: 0 auto;
		margin-top: 0.16rem;
		font-family: PingFangSC-Regular;
		font-size: 0.12rem;
		color: rgba(0,0,0,0.26);
		text-align: center;
		line-height: 0.18rem;
	}
	.ad_img {
		margin-bottom: 0.08rem;
		width: 100%;
		height: 1rem;
		background: #FFFFFF;
		box-shadow: 0 0.04rem 0.16rem 0 rgba(0,0,0,0.04);
		border-radius: 0.04rem;
	}
	.loan_box {
		/*position: fixed;
		bottom: 0;*/
		position: relative;
		width: 100%;
	}
	.loan {
		padding: 0.17rem 0.16rem;
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background: url('../../assets/img/advertising-background.png') center no-repeat;
		background-size: 100% 100%;
	}
	.loan_name {
		font-family: PingFangSC-Medium;
		font-size: 0.14rem;
		color: rgba(0,0,0,0.60);
		text-align: left;
		line-height: 0.18rem;
	}
	.loan_info {
		
	}
	.loan_limit {
		font-family: PingFangSC-Semibold;
		font-size: 0.34rem;
		color: #FF5D50;
		text-align: center;
		line-height: 0.48rem;
	}
	.loan_limit span {
		margin-right: 0.04rem;
		font-family: PingFangSC-Medium;
		font-size: 0.18rem;
		color: #FF5D50;
		text-align: center;
		line-height: 0.25rem;
	}
	.loan_intro {
		width: 1.6rem;
		font-family: PingFangSC-Regular;
		font-size: 0.12rem;
		color: rgba(0,0,0,0.38);
		line-height: 0.14rem;
		text-align: center;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		transform: scale(0.83);
	}
	.loan_btn {
		width: 0.76rem;
		background-image: linear-gradient(-270deg, #51AAF9 0%, #3E4FEC 100%);
		box-shadow: 0 0.02rem 0.04rem 0 rgba(62,79,236,0.26);
		border-radius: 0.18rem;
		font-family: PingFangSC-Medium;
		font-size: 0.13rem;
		color: #FFFFFF;
		line-height: 0.36rem;
		text-align: center;
	}
	.loan_comment {
		position: absolute;
		top: -0.1rem;
		left: 0;
		padding: 0 0.08rem;
		background: #F1E9E0;
		border-radius: 0.04rem 0 0.04rem 0;
		font-family: PingFangSC-Regular;
		font-size: 0.12rem;
		color: #B79C7D;
		line-height: 0.2rem;
	}
	.loan_logo {
		margin: 0 auto;
		display: block;
		width: 0.36rem;
		height: 0.36rem;
	}
	.loan_name_text {
		margin-top: 0.04rem;
		font-family: PingFangSC-Medium;
		font-size: 0.12rem;
		color: rgba(0,0,0,0.60);
		text-align: center;
		line-height: 0.18rem;
	}
</style>


