import Vue from 'vue'
import Router from 'vue-router'

// 一级路由
import Index from '@/news/index2'
import ProdDetail from '@/news/prodDetail'

// 二级路由
import Home from '@/news/tabPage/index'
import News from '@/news/tabPage/news'
import Koubei from '@/news/tabPage/koubei'
import Class from '@/news/tabPage/class'

Vue.use(Router)

export const tabChildren = [
  // {
  //   path: '/index',
  //   name: 'index1',
  //   component: Home,
  //   meta: {
  //     name: '精选',
  //     saEvent: 'specialT_click'
  //   }
  // }, 
  {
    path: '/news',
    name: 'news',
    component: News,
    meta: {
      name: '快报',
      saEvent: 'hotnewsT_click'
    }
  }, {
    path: '/koubei',
    name: 'koubei',
    component: Koubei,
    meta: {
      name: '口碑',
      saEvent: 'mouthT_click'
    }
  }, {
    path: '/newsClass',
    name: 'newsClass',
    component: Class,
    meta: {
      name: '教程',
      saEvent: 'professorT_click'
    }
  }
];

// version2
export default [
  {
    path: '/news',
    redirect: '/index',
    component: Index,
    children: tabChildren
  }, {
    path: '/prodDetail',
    name: 'prodDetail',
    component: ProdDetail,
  }
]



