<template>
	<div class="content">
		<div class="prod_box">
			<div class="prod_list">
				<div class="prod_item" v-for="(item, index) in prodList" :key="item.id" @click="comment(item, index)">
					<img class="prod_logo" :src="item.logoUrl">
					<div class="prod_name">{{item.platformName}}</div>
					<div class="prod_rate">
						<span class="prod_rate_text">通过率</span>
						<span class="prod_rate_val">{{parseInt(100 * item.passRate)}}%</span>
					</div>
					<div class="prod_rate_step" :class="{origin: item.passRate >= 0.7, green: item.passRate < 0.7}">
						<div class="prod_rate_step_val" :style="{width: item.passRate * 100 + '%'}"></div>
					</div>
					<div class="prod_grade">
						<div class="prod_grade_text">评分</div>
						<img 
							class="prod_grade_icon" 
							v-for="(item_, index_) in handleProdGrade(item.score)" 
							:key="index_" 
							:src="item_ ? require('../../assets/img/ic_ratestar_active.png') : require('../../assets/img/ic_ratestar_inactive.png')">
						<div class="prod_grade_num">{{item.score}}</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import methods from '../../mixins/methods'
	import api from '../../api/classroom'
	export default {
	  	name: 'index',
	  	components: {
	  		
	  	},
	  	data () {
	    	return {
	    		prodList: [],
		    }
	  	},
	  	mixins: [methods],
	  	computed: {
	  		
	  	},
	  	methods: {
	  		comment(prod, index) {
	  			sa.track('mouthP_click');
	  			sa.track("mot_click", {
			        loanname: prod.platformName
				});
				this.$router.push({
					path: '/prodDetail',
					query: {
						id: prod.id
					}
				})
	  		},
	  		getList() {
	  			api.requestGet('headline-product/products', {}).then(res => {
	  				if(res.code == 0) {
	  					this.prodList = res.data.sort((item1, item2) => {
	  						return item2.shelfTime - item1.shelfTime
	  					});
	  					for(var i = 0; i < this.prodList.length; i++) {
	  						if(i != 0) {
	  							console.log(this.prodList[i].shelfTime - this.prodList[i-1].shelfTime)
	  						}
	  						
	  					}
		        		this.$emit('hide');
	  				}else {
	  					this.$message({
	  						text: res.msg,
	  						type: 'error'
	  					})
	  				}
	  			})
	  		}
	  	},
	  	created() {
	  		sa.registerPage({
				activepagename: 'mouthPage',
				h5TabName: 'koubeiTab'
			});
			sa.track('webview');
	  		this.getList();
			window.scrollTo(0,0);
	  	},
	  	mounted() {
	  		
	 	},
	}
</script>

<style>
	.prod_box {
		padding: 0 0.16rem;
		width: 100%;
		box-sizing: border-box;
	}
	.prod_list {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.prod_item {
		margin-top: 0.08rem;
		padding: 0.16rem;
		padding-bottom: 0.12rem;
		box-sizing: border-box;
		width: 1.6rem;
		display: flex;
		flex-direction: column;
		align-items: center;background: #FFFFFF;
		box-shadow: 0 0.04rem 0.16rem 0 rgba(0,0,0,0.04);
		border-radius: 0.04rem;
	}
	.prod_logo {
		width: 0.64rem;
		height: 0.64rem;
		border-radius: 50%;
	}
	.prod_name {
		margin-top: 0.08rem;
		font-family: PingFangSC-Medium;
		font-size: 0.14rem;
		color: rgba(0,0,0,0.87);
		text-align: left;
		line-height: 0.2rem;
	}
	.prod_rate {
		margin-top: 0.12rem;
		width: 100%;
		font-family: PingFangSC-Regular;
		font-size: 0.12rem;
		color: #F37121;
		text-align: right;
		line-height: 0.18rem;
		display: flex;
		justify-content: space-between;
	}
	.prod_rate_text {
		color: rgba(0,0,0,0.26);
	}
	.prod_rate_step {
		position: relative;
		margin-top: 0.06rem;
		width: 100%;
		height: 0.06rem;
		border-radius: 0.03rem;
	}
	.origin {
		background: #F1E9E0;
	}
	.green {
		background: #E5F2F3;
	}
	.prod_rate_step_val {
		position: absolute;
		left: 0;
		bottom: 0;
		height: 0.08rem;
		border-radius: 0.04rem;
	}
	.origin .prod_rate_step_val {
		background: linear-gradient(270deg, #F37121 0%, #F9A01B 100%);
	}
	.green .prod_rate_step_val {
		background: linear-gradient(270deg, #1FD7B7 0%, #38DABC 100%);
	}
	.prod_grade {
		margin-top: 0.08rem;
		width: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	.prod_grade_text {
		margin-right: 0.08rem;
		font-family: PingFangSC-Regular;
		font-size: 0.12rem;
		color: rgba(0,0,0,0.26);
		text-align: left;
		line-height: 0.18rem;
	}
	.prod_grade_icon {
		margin: 0 0.01rem;
		width: 0.1rem;
		height: 0.1rem;
	}
	.prod_grade_num {
		flex: 1;
		font-family: PingFangSC-Regular;
		font-size: 0.12rem;
		color: rgba(0,0,0,0.38);
		text-align: right;
		line-height: 0.18rem;
	}
</style>


