// 初始化获取token
AppClient.prototype.getInitData = function (callbackName, callback) {
  console.log('--457-1--')
	const data = {
		"callback": callbackName
	};
	if (this.isAndroid) {
    console.log('--457-2--')
		this.appObj.getInitDataAction(JSON.stringify(data));
	} else {
		this.appObj.callHandler("getInitDataAction", data, callback);
	}
};

