				<div class="class_top">
					<div class="class_top_intro">
						<div class="class_top_text">{{classList.length >= 6 ? classList[5].courseName : '网贷被拒应该如何优化资质？'}}</div>
						<div class="class_top_text2">揭秘拒贷真相，{{classList.length}}课搞定下款</div>
					</div>
					<img class="class_top_banner" src="../../assets/img/img_course_cover01.png">
				</div>
				<div class="line2"></div>
				<div class="class_top_listen">
					<div class="class_top_listen_intro">
						<div class="class_top_listen_intro_title">适合人群</div>
						<div class="class_top_listen_intro_text">网贷小白、网贷黑户</div>
					</div>
					<div class="line3"></div>
					<div class="class_top_listen_intro" style="flex: 1;">
						<div class="class_top_listen_intro_title">教程</div>
						<div class="class_top_listen_intro_text">{{classList.length}}节</div>
					</div>
