</template>

<script>
  	import api from '../../api/classroom'
	export default {
	  	name: 'index',
	  	components: {
	  		
	  	},
	  	data () {
	    	return {
	    		classList: [],
	    		studyNum: 0,
	    		ad: {},
	    		limitList: [10000, 40000, 20000, 60000, 200000, 30000, 50000, 100000],
	    		bottom: localStorage.getItem('bottom'),
	    		qjList: []
		    }
	  	},
	  	computed: {
	  		playNum() {
	  			let length = this.qjList.length;
	  			let sum = 0
	  			for(let i = 0; i < length; i++) {
	  				sum += this.qjList[i].playNum + 185
	  			}
	  			return sum * 13
	  		},
	  		usefulNum() {
	  			let length = this.qjList.length;
	  			let sum = 0
	  			for(let i = 0; i < length; i++) {
	  				sum += this.qjList[i].usefulNum + 96
	  			}
	  			return sum * 8
	  		}
	  	},
	  	methods: {
	  		getOrder() {
	  			let classList = JSON.parse(localStorage.getItem('classList'));
		    	let obj = {
		    		randomId: localStorage.getItem('randomId'),
		    		courseSource: 1
		    	}
		        api.requestClass('class/course/getList2', JSON.stringify(obj), 'application/json; charset=utf-8').then(res => {
		        	if(res.code == 0) {
		        		let classList = [...res.data.freeCourseList, ...res.data.netLoanCourseList];
		        		classList[0].duration = '03:06';
		        		classList[1].duration = '03:22';
		        		classList[2].duration = '03:14';
		        		this.classList = classList;
		        		this.$emit('hide');
		        	}else {
		        		this.$message({
		        			text: res.msg,
		        			type: 'warn'
		        		})
		        	}
		        })
		    },
	  		getQj() {
		    	let obj = {
		    		randomId: localStorage.getItem('randomId'),
		    		courseSource: 2
		    	}
		        api.requestClass('class/course/getList2', JSON.stringify(obj), 'application/json; charset=utf-8').then(res => {
		        	if(res.code == 0) {
		        		this.qjList = [...res.data.freeCourseList, ...res.data.netLoanCourseList];
		        		let studyNum = 0;
			  			for(var i = 0, len = this.qjList.length; i < len; i++) {
			  				studyNum += this.qjList[i].playNum;
			  			}
			  			this.studyNum = parseInt(studyNum * 13 * 0.4);
		        	}else {
		        		this.$message({
		        			text: res.msg,
		        			type: 'warn'
		        		})
		        	}
		        })
		    },
	  		getAd() {
	  			api.requestGet('headline/ads', {locate: 'xiaobaizixun'}).then(res => {
	  				if(res.code == 0) {
	  					let adIndex = localStorage.getItem('adIndex') ? parseInt(localStorage.getItem('adIndex')) + 1 : 0;	  	
	  					localStorage.setItem('adIndex', adIndex % res.data.length);
	  					this.ad = res.data[adIndex % res.data.length];
	  					this.ad.limit = this.limitList[adIndex % this.limitList.length];
	  				}else {
	  					this.$message({
	  						text: res.msg,
	  						type: 'error'
	  					})
	  				}
	  			})
	  		},
		    goClass(courseName) {
		    	let path = {
		    		'proA': '/huabei',
		    		'proB': '/guide'
		    	}
		    	sa.track('courseP_click')
		    	sa.track('zmListen_click', {
		    		courseName
		    	});
		    	this.$router.push(path[courseName]);
		    },
		    apply() {
		    	sa.track('courseP_click')
		    	sa.track('unionAd_click', {
		    		adName: 'courseEnd'
		    	})
		    	setTimeout(() => {
		    		location.href = this.ad.adUrl
		    	}, 300)
		    	
		    }
	  	},
	  	created() {
	  		sa.registerPage({
				activepagename: 'coursePage',
				h5TabName: 'kechengTab'
			});
			sa.track('webview');
	  		this.getOrder();
	  		this.getQj();
	  		this.getAd();
			window.scrollTo(0,0);
	  	},
	  	mounted() {
	  		
	 	},
	}
</script>

