									class="loan_grade_star" 
									v-for="(item_, index_) in handleProdGrade(item.score)" 
									:key="index_" 
									:src="item_ ? require('../../assets/img/ic_ratestar_active.png') : require('../../assets/img/ic_ratestar_inactive.png')">
								<div class="loan_grade_num">{{item.score}}</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="class_guide">
			<div class="loan_top">
				<div class="loan_title" style="color: #916141;">精品教程</div>
				<div class="loan_more" @click="$router.push({name: 'newsClass'})">
					<span>更多教程</span>
					<img class="loan_arrow" src="../../assets/img/ic_chevron_right.png">
				</div>
			</div>
			<div class="class_box">
				<div class="class_top">
					<img class="class_top_banner" src="../../assets/img/img_course_cover01.png">
					<div class="class_top_intro">
						<div class="class_top_text">网贷被拒应该如何优化资质？</div>
						<div class="class_top_listen">
							<div class="class_top_listen_num">{{this.studyNum}}人<br>正在学习</div>
