								<div class="class_top_listen_btn_icon_box">
									<img class="class_top_listen_btn_icon" src="../../assets/img/course_play_w.png">
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="class_content">
					<div class="class_lsit" @click="goClass">
						<div class="class_item" v-for="(item, index) in classList" :key="item.classCourseId" v-if="index < 3">
							<div class="class_name">0{{index + 1}} {{item.courseName}}</div>
							<!-- <div class="class_duration">{{item.duration}}</div> -->
						</div>
					</div>
					<div class="class_more" @click="goClass">
						<span>共{{classList.length}}节课, 查看更多</span>
						<img class="class_more_arrow" src="../../assets/img/ic_chevron_right_orange.png">
					</div>
				</div>
			</div>
