		align-items: center;
		font-family: PingFangSC-Regular;
		font-size: 12px;
		color: rgba(0,0,0,0.60);
		text-align: left;
		line-height: 18px;
	}
	.class_item:last-child {
		border: none;
	}
	.class_name {
		flex: 1;
		color: rgba(0,0,0,0.60);
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.class_duration {
		margin-left: 0.16rem;
		color: rgba(0,0,0,0.26);
	}
	.class_more {
		margin-top: 0.06rem;
		display: flex;
		justify-content: center;
		font-family: PingFangSC-Regular;
		font-size: 0.123rem;
		color: #F37121;
		text-align: right;
		line-height: 0.18rem;
	}
	.class_more_arrow {
		width: 0.16rem;
		height: 0.16rem;
	}
</style>


